import kotlin.experimental.ExperimentalObjCName
import kotlin.native.ObjCName

@OptIn(
  ExperimentalObjCName::class,
  RedwoodCodegenApi::class,
)
@ObjCName(
  "SchemaWidgetSystem",
  exact = true,
)
public class SchemaWidgetSystem<W : Any>(
  override val Schema: SchemaWidgetFactory<W>,
  override val RedwoodLayout: RedwoodLayoutWidgetFactory<W>,
) : WidgetSystem<W>, SchemaWidgetFactoryOwner<W>, RedwoodLayoutWidgetFactoryOwner<W> {
  override fun apply(`value`: W, element: Modifier.UnscopedElement) {
    SchemaWidgetFactoryOwner.apply(Schema, value, element)
    RedwoodLayoutWidgetFactoryOwner.apply(RedwoodLayout, value, element)
  }
}

@Deprecated(
  "Renamed to SchemaWidgetSystem",
  ReplaceWith("SchemaWidgetSystem", "com.example.redwood.counter.widget.SchemaWidgetSystem"),
)
public typealias SchemaWidgetFactories<W> = SchemaWidgetSystem<W>

/**
 * @suppress For generated code usage only.
 */
@RedwoodCodegenApi
public interface SchemaWidgetFactoryOwner<W : Any> : WidgetFactoryOwner<W> {
  public val Schema: SchemaWidgetFactory<W>

  public companion object {
    @Suppress("UNUSED_PARAMETER")
    public fun <W : Any> apply(
      factory: SchemaWidgetFactory<W>,
      `value`: W,
      element: Modifier.UnscopedElement,
    ) {
    }
  }
}
